
body{width:100%;height:100%;font-family:"Lora","Helvetica Neue",Helvetica,Arial,sans-serif;color:white;background-color:black
}
html{width:100%;height:100%
}
h1,h2,h3,h4,h5,h6{margin:0 0 35px;text-transform:uppercase;font-family:"Montserrat","Helvetica Neue",Helvetica,Arial,sans-serif;font-weight:700;letter-spacing:1px
}
p{margin:0 0 25px;font-size:18px;line-height:1.5
}
@media (min-width: 768px){
p{margin:0 0 35px;font-size:20px;line-height:1.6
}
}
a{color:#42DCA3;transition:all .2s ease-in-out
}
a:hover,a:focus{text-decoration:none;color:#1d9b6c
}
.btn{text-transform:uppercase;font-family:"Montserrat","Helvetica Neue",Helvetica,Arial,sans-serif;font-weight:400;transition:all .3s ease-in-out;border-radius:0
}
.btn-default{border:1px solid #42DCA3;color:#42DCA3;background-color:transparent
}
.btn-default:hover,.btn-default:focus{border:1px solid #42DCA3;outline:none;color:black;background-color:#42DCA3
}
ul.banner-social-buttons{margin-top:0
}
@media (max-width: 1199px){
ul.banner-social-buttons{margin-top:15px
}
}
@media (max-width: 767px){
ul.banner-social-buttons li{display:block;margin-bottom:20px;padding:0
}
ul.banner-social-buttons li:last-child{margin-bottom:0
}
}
footer{padding:50px 0
}
footer p{margin:0
}
::-moz-selection{text-shadow:none;background:#fcfcfc;background:rgba(255,255,255,0.2)
}
::selection{text-shadow:none;background:#fcfcfc;background:rgba(255,255,255,0.2)
}
img::-moz-selection{background:transparent
}
img::selection{background:transparent
}
img::-moz-selection{background:transparent
}
body{-webkit-tap-highlight-color:rgba(255,255,255,0.2)
}
.main-content{padding:25px;width:100%;max-width:900px;margin-left:auto;margin-right:auto
}
.md-super-inset{padding-left:20px
}
.btn-norm{background-color:#2196f3;color:white !important;border-color:#2196f3
}
#page-top{width:100%;height:100%;color:white
}
#page-top .light{font-weight:400
}
#page-top .navbar-custom{margin-bottom:0;border-bottom:1px solid rgba(255,255,255,0.3);text-transform:uppercase;font-family:"Montserrat","Helvetica Neue",Helvetica,Arial,sans-serif;background-color:black
}
#page-top .navbar-custom .navbar-toggle{padding:4px 6px;font-size:16px;color:white
}
#page-top .navbar-custom .navbar-toggle:focus,#page-top .navbar-custom .navbar-toggle:active{outline:none
}
#page-top .navbar-custom .navbar-brand{font-weight:700
}
#page-top .navbar-custom .navbar-brand:focus{outline:none
}
#page-top .navbar-custom a{color:white
}
#page-top .navbar-custom .nav li a{transition:background .3s ease-in-out
}
#page-top .navbar-custom .nav li a:hover{color:rgba(255,255,255,0.8);outline:none;background-color:transparent
}
#page-top .navbar-custom .nav li a:focus,#page-top .navbar-custom .nav li a:active{outline:none;background-color:transparent
}
#page-top .navbar-custom .nav li.active{outline:none
}
#page-top .navbar-custom .nav li.active a{background-color:rgba(255,255,255,0.3)
}
#page-top .navbar-custom .nav li.active a:hover{color:white
}
@media (min-width: 768px){
#page-top .navbar-custom{padding:20px 0;border-bottom:none;letter-spacing:1px;background:transparent;transition:background .5s ease-in-out,padding .5s ease-in-out
}
#page-top .navbar-custom.top-nav-collapse{padding:0;background:black;border-bottom:1px solid rgba(255,255,255,0.3)
}
}
#page-top .intro{display:table;width:100%;height:auto;padding:100px 0;text-align:center;color:white;background:linear-gradient(to bottom, rgba(0,0,0,0.35) 0%, rgba(0,0,0,0.5) 59%, #000 100%),url(/static/img/intro-bg.jpg) no-repeat bottom center scroll;background-color:black;background-size:cover;-o-background-size:cover
}
#page-top .intro .intro-body{display:table-cell;vertical-align:middle
}
#page-top .intro .intro-body .brand-heading{font-size:40px
}
#page-top .intro .intro-body .intro-text{font-size:18px
}
@media (min-width: 768px){
#page-top .intro{height:100%;padding:0
}
#page-top .intro .intro-body .brand-heading{font-size:100px
}
#page-top .intro .intro-body .intro-text{font-size:26px
}
}
#page-top .btn-circle{width:70px;height:70px;margin-top:15px;padding:7px 16px;border:2px solid white;border-radius:100% !important;font-size:40px;color:white;background:transparent;transition:background .3s ease-in-out
}
#page-top .btn-circle:hover,#page-top .btn-circle:focus{outline:none;color:white;background:rgba(255,255,255,0.1)
}
#page-top .btn-circle i.animated{-webkit-transition-property:-webkit-transform;-webkit-transition-duration:1s;-moz-transition-property:-moz-transform;-moz-transition-duration:1s
}
#page-top .btn-circle:hover i.animated{-webkit-animation-name:pulse;-moz-animation-name:pulse;-webkit-animation-duration:1.5s;-moz-animation-duration:1.5s;-webkit-animation-iteration-count:infinite;-moz-animation-iteration-count:infinite;-webkit-animation-timing-function:linear;-moz-animation-timing-function:linear
}
#page-top .content-section{padding-top:100px
}
#page-top .download-section{width:100%;padding:50px 0;color:white;background-color:#2b2b2b;background-size:cover;-o-background-size:cover
}
#page-top #map{width:100%;height:200px;margin-top:100px
}
@media (min-width: 767px){
#page-top .content-section{padding-top:250px
}
#page-top .download-section{padding:100px 0
}
#page-top #map{height:400px;margin-top:250px
}
}



